@import url(https://fonts.googleapis.com/css?family=Calistoga&display=swap);
@import url(https://fonts.googleapis.com/css?family=Calistoga&display=swap);
@import url(https://fonts.googleapis.com/css?family=Josefin+Sans|Rajdhani&display=swap);
@import url(https://fonts.googleapis.com/css?family=Racing+Sans+One&display=swap);
button {
    outline: none;
}

.todo {
    display: grid;
    padding: 1.8rem 2.5rem;
    grid-column-gap: 1rem;
    border-radius: 4px;
    box-shadow: 0 0 0 6px rgba(190, 137, 77, 0.473), 0 1rem 1rem 1px rgba(0, 0, 0, 0.479);
    grid-template-columns: 1fr minmax(-webkit-min-content, 10rem);
    grid-template-columns: 1fr minmax(min-content, 10rem);
    align-items: start;
    background-color: rgba(255, 255, 255, 1);
}

@media only screen and (max-width: 35em) {
    .todo {
        padding: 1rem;
        grid-template-columns: auto;
        border-radius: 0 4px 4px 0 ;
        grid-row-gap: 2rem;
    }
    .todo.done {
        border-radius: 4px 0 0 4px;
    }
}

.todo-main {
    display: flex;
    flex-direction: column;
}

.todo-date {
    font-size: 1.5rem;
    color: rgba(0, 0, 0, .5);
    margin-bottom: 1rem;
    font-family: 'Calistoga', cursive;
}

@media only screen and (max-width: 35em) {
    .todo-date {
        font-size: .8rem;
        margin-bottom: .5rem;
    }
}

.todo-content {
    font-family: 'Calistoga', cursive;
    margin-right: auto;
    font-size: 2rem;
    color: rgba(73, 0, 92, 0.664);
    word-break: break-all;
    font-weight: 900;
    text-transform: capitalize;
    height: 50%;
    border: 2px solid transparent;
}

@media only screen and (max-width: 35em) {
    .todo-content {
        font-size: 1.1rem;
    }
}

.todo-content-editing-form {
    flex: 1 1;
    display: flex;
    align-items: center;
    margin-right: 2rem;
}

.todo-content-editing-input {
    font-family: 'Calistoga', cursive;
    width: 100%;
    border-radius: 7px;
    height: 50%;
    border: 2px solid transparent;
    -webkit-transition: all .15s linear;
    transition: all .15s linear;
    font-weight: 100;
    padding: 1.5rem 1rem;
    font-size: 2rem;
    background: rgba(117, 2, 184, 0.349);
    outline: none;
}

@media only screen and (max-width: 35em) {
    .todo-content-editing-input {
        font-size: 1rem;
    }
}

.todo.done > .todo-content {
    text-decoration: line-through;
    color: rgba(0, 0, 0, 0.253);
    font-weight: 100;
}

.todo-button-group {
    display: flex;
    justify-content: flex-end;
}

@media only screen and (max-width: 35em) {
    .todo-button-group > button {
        width: auto;
        margin-left: 1rem;
    }
    .todo-button-group  * {
        font-size: 1rem;
    }
}

.todo-edit-button {
    border: none;
    width: 3rem;
    cursor: pointer;
    background-color: transparent;
}

@media only screen and (max-width: 35em) {
    
}

.todo-done-button {
    border: none;
    font-family: 'Josefin Sans', sans-serif;
    background-color: transparent;
    color: rgba(0, 0, 0, .8);
    cursor: pointer;
    width: 3rem;
    border-radius: 5px 0 0 5px;
    background-repeat: no-repeat;
}

.todo-discard-button {
    border: none;
    background-color: transparent;
    width: 3rem;
    cursor: pointer;
    border-radius: 0 5px 5px 0;
}

i {
    color: rgba(117, 22, 50, 0.55);
    -webkit-transition: all .15s linear;
    transition: all .15s linear;
}

button:hover > i {
    color: rgba(167, 0, 182, 0.822);
}
* {
    box-sizing: border-box;
}

.list-complex {
    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 3rem;
    grid-column-gap: 5rem;
    grid-row: 3 / auto;
}

@media only screen and (max-width: 35em) {
    .list-complex {
        grid-column-gap: 0rem;
    }
}

.list {
    display: grid;
    align-content: start;
    grid-template-columns: repeat( 10, 1fr);      
    list-style-type: none;
    grid-row-gap: 2rem;
}  

.list-undone > .todo-item {
    grid-column: 2 /  10;
}

.list-done > .todo-item {
    grid-column: -2 / -10;
}

@media only screen and (max-width: 35em) {
    .list-undone > .todo-item {
        grid-column: 1 /  10;
    }
}

@media only screen and (max-width: 35em) {
    .list-done > .todo-item {
        grid-column: -1 /  -10;
    }
}

.list-heading {
    display: flex;   
    grid-row: 1 / span 1;
    position: relative;
    justify-self: stretch;
    margin-bottom: 3rem;
    letter-spacing: 1rem;
    z-index: 10;
    background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(202, 85, 49, 0.87)), to(rgb(75, 2, 82)));
    background-image: linear-gradient(rgba(202, 85, 49, 0.87), rgb(75, 2, 82));
    color: rgba(255, 255, 255, 1);
    text-shadow: 3px 6px 1px rgba(172, 172, 172, 0.61);
    font-size: 5rem;
    font-family: 'Calistoga', cursive;
    padding: 2rem;
}

@media only screen and (max-width: 35em) {
    .list-heading {
        font-size: 1.5rem;
        padding: 1rem 1.4rem;
        letter-spacing: .1rem;
    }
}

.list-heading-undone {
    grid-column: 1 / 10;
    padding-right: 5rem;
    border-radius:0 200px 200px 0;
    text-align: right;
    box-shadow: 10px 15px 40px 10px rgba(0, 0, 0, .35), 0 0 0 10px rgb(59, 0, 77), 0 2px 0 11px rgba(255, 255, 255, .75);
}   

.list-heading-done {
    grid-column: -1 / -10;
    padding-left: 5rem;
    border-radius: 200px 0 0 200px;
    text-align: left;
    box-shadow: -10px 15px 40px 10px rgba(0, 0, 0, .35), 0 0 0 10px rgb(59, 0, 77), 0 2px 0 11px rgba(255, 255, 255, .75);
}

.list-heading > h2 {
    width: 100%;
    line-height: 1;
}
.todo-container {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    z-index: 1000;
    grid-row: 1 / span 2;
    grid-column: 1 / span 1;
}

.todo-container.shrink-on-focus-out:not(:focus-within) {
    grid-row: 1 / span 1;
}

.todo-container.shrink-on-focus-out > .todo-input::-webkit-input-placeholder {
    color: transparent;
}

.todo-container.shrink-on-focus-out > .todo-input::-moz-placeholder {
    color: transparent;
}

.todo-container.shrink-on-focus-out > .todo-input:-ms-input-placeholder {
    color: transparent;
}

.todo-container.shrink-on-focus-out > .todo-input::-ms-input-placeholder {
    color: transparent;
}

.todo-container.shrink-on-focus-out > .todo-input::placeholder {
    color: transparent;
}

.todo-form {
    display: flex;
    height: 100%;
    font-family: 'Josefin Sans', sans-serif;
}

.todo-span {
    overflow: hidden;
    position: relative;
    flex: 1 1;
}

.todo-input {
    position: relative;
    border: 0;
    width: 100%;
    height: 100%;
    color: rgba(255, 255, 255, 0.89);
    text-shadow: 2px 1px 3px rgba(255, 38, 255, 0.678), -2px 1px 3px rgba(73, 211, 119, 0.623), 20px 15px 10px white, 20px 15px 15px white, 1px 2px  50px rgb(159, 75, 255);
    background: transparent;
    outline: none;
    font-family: 'Rajdhani', sans-serif;
    font-size: 6rem;
    text-transform: lowercase;
    padding: 0 1rem;
    -webkit-transition: all .15s linear;
    transition: all .15s linear;
}

@media only screen and (max-width: 35em) {
    .todo-input {
        font-size: 2.5rem;
        padding: 0 .4rem;
    }
}

.todo-input:focus {
    background-image: -webkit-gradient(linear, right top, left top, from(rgba(255, 173, 255, .45)), to(rgba(255, 173, 255, .1)));
    background-image: linear-gradient(to left, rgba(255, 173, 255, .45), rgba(255, 173, 255, .1));
    box-shadow: 0 0 15px 6px black;
}

.todo-input.shrink-on-focus-out:not(:focus) {
    font-size: 4rem;
}

@media only screen and (max-width: 35em) {
    .todo-input.shrink-on-focus-out:not(:focus) {
        font-size: 2rem;
    }
}

.todo-input.shrink-on-focus-out::-webkit-input-placeholder {
    color: transparent;
    text-shadow: none;
}

.todo-input.shrink-on-focus-out::-moz-placeholder {
    color: transparent;
    text-shadow: none;
}

.todo-input.shrink-on-focus-out:-ms-input-placeholder {
    color: transparent;
    text-shadow: none;
}

.todo-input.shrink-on-focus-out::-ms-input-placeholder {
    color: transparent;
    text-shadow: none;
}

.todo-input.shrink-on-focus-out::placeholder {
    color: transparent;
    text-shadow: none;
}

.todo-span:focus-within + .add-todo-button {
    visibility : visible;
    opacity: 1;
}

.todo-input:focus::-webkit-input-placeholder {
   color: transparent;
   text-shadow: none;
}

.todo-input:focus::-moz-placeholder {
   color: transparent;
   text-shadow: none;
}

.todo-input:focus:-ms-input-placeholder {
   color: transparent;
   text-shadow: none;
}

.todo-input:focus::-ms-input-placeholder {
   color: transparent;
   text-shadow: none;
}

.todo-input:focus::placeholder {
   color: transparent;
   text-shadow: none;
}

.todo-input:not(:focus) {
   color: transparent;
    text-shadow: 2px 2px 3px rgba(0, 0, 0, .1);
}

.todo-span:not(:focus-within)::after {
    content: '';
    position: absolute;
    height: 100%;
    width: 50%;
    background-image: linear-gradient(25deg, transparent, transparent, rgba(238, 255, 87, 0.459), rgba(41, 119, 192, 0.2), transparent);
    -webkit-animation: swoosh 7s ease-out infinite;
            animation: swoosh 7s ease-out infinite; 

}

.todo-input::-webkit-input-placeholder {
    color: white;
    padding-left: 1rem;
    text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    -webkit-animation: power-flicker 5s linear infinite;
            animation: power-flicker 5s linear infinite;
}

.todo-input::-moz-placeholder {
    color: white;
    padding-left: 1rem;
    text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    animation: power-flicker 5s linear infinite;
}

.todo-input:-ms-input-placeholder {
    color: white;
    padding-left: 1rem;
    text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    animation: power-flicker 5s linear infinite;
}

.todo-input::-ms-input-placeholder {
    color: white;
    padding-left: 1rem;
    text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    animation: power-flicker 5s linear infinite;
}

.todo-input::placeholder {
    color: white;
    padding-left: 1rem;
    text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    -webkit-animation: power-flicker 5s linear infinite;
            animation: power-flicker 5s linear infinite;
}

.add-todo-button {
    flex: 0 0 10vw;
    visibility : hidden;
    opacity: 0;
    border: 0;
    color: rgba(0, 0, 0, 0.404);
    font-weight: 100;
    font-size: 8rem;
    font-family: 'Josefin Sans', sans-serif;
    outline: none;
    cursor: pointer;
    -webkit-transition: all .15s linear;
    transition: all .15s linear;
}

@media only screen and (max-width: 35em) {
    .add-todo-button {
        flex: .01 0 10vw;
        font-size: 4rem;
    }
}

.add-todo-button-enabled {
    position: relative;
    background-image: radial-gradient(circle at 0% 30%, rgb(218, 31, 255) 10%, rgb(122, 61, 19) 70%);
    background-size: 200%;
    background-repeat: no-repeat;
    overflow: hidden;
    color: rgba(0, 0, 0, 0.582);
}

.add-todo-button-enabled::after {
    content:'';
    position: absolute;
    height: 10rem;
    width: 200%;
    -webkit-transform: rotate(-55deg);
            transform: rotate(-55deg);
    background-image: -webkit-gradient( linear, left top, left bottom, from(transparent), color-stop(rgba(255, 255, 255, .2)),   color-stop(rgba(255, 255, 255, .85)), color-stop(rgba(255, 255, 255, .2)), to(transparent)   );
    background-image: linear-gradient( transparent, rgba(255, 255, 255, .2),   rgba(255, 255, 255, .85), rgba(255, 255, 255, .2), transparent   );
    -webkit-animation: button-spark 3.5s linear infinite forwards;
            animation: button-spark 3.5s linear infinite forwards;
}

.add-todo-button-disabled {
    cursor: auto;
    background: rgba(0, 0, 0, 0.658);
    color: gray;
}

.add-todo-button-enabled:hover {
    color: rgb(255, 234, 240);
    text-shadow: 0 0 20px rgba(248, 193, 255, 1), 0 0 40px rgba(248, 193, 255, 1), 0 0 60px rgba(248, 193, 255, 1), 0 0 100px rgba(248, 193, 255, 1);
}

.add-todo-button-enabled:hover::after {
    width: 11px;
    height: 11px;
    background: rgba(226, 163, 255, .8);
    box-shadow: 0 0 10px 1px rgba(0, 0, 0, 0.39);
    -webkit-transform: none;
            transform: none;
    -webkit-animation: button-hovered 3s infinite linear;
            animation: button-hovered 3s infinite linear;
}   

@-webkit-keyframes power-flicker {
    0% {opacity: 0;
        color: white;
        text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    } 20% {
        color: transparent;
        text-shadow: none;
    } 22% {
        color: white;
        text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    } 25% {
        color: transparent;
        text-shadow: none;
    } 35% {
        color: white;
        text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    } 36% {
        color: transparent;
        text-shadow: none;
    } 37% {
        color: white;
        text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    } 50% {
        color: transparent;
        text-shadow: none;
    } 100% {
        color: white;
        text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    }
}   

@keyframes power-flicker {
    0% {opacity: 0;
        color: white;
        text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    } 20% {
        color: transparent;
        text-shadow: none;
    } 22% {
        color: white;
        text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    } 25% {
        color: transparent;
        text-shadow: none;
    } 35% {
        color: white;
        text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    } 36% {
        color: transparent;
        text-shadow: none;
    } 37% {
        color: white;
        text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    } 50% {
        color: transparent;
        text-shadow: none;
    } 100% {
        color: white;
        text-shadow: 0 0 5px white, 0 0 10px white, 0 0 20px #9600b4, 0 0 30px #9600b4, 0 0 45px #9600b4;
    }
}

@-webkit-keyframes button-spark {
    0% {
        left: -200%;
        top: -100%;
    }45% {
        left: -200%;
        top: -150%;
    }65% {
        left: 100%;
        top: 100%;
    }100% {
        left: 100%;
        top: 100%;
    }
}

@keyframes button-spark {
    0% {
        left: -200%;
        top: -100%;
    }45% {
        left: -200%;
        top: -150%;
    }65% {
        left: 100%;
        top: 100%;
    }100% {
        left: 100%;
        top: 100%;
    }
}

@-webkit-keyframes button-hovered {
    0%, 100%{
        top: 0;
        left: 0;
    }25%{
        top: 100%;
        left: 0;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }50%{
        top: 100%;
        left: 100%;
        -webkit-transform: translateY(-100%) translateX(-100%);
                transform: translateY(-100%) translateX(-100%);
    }75%{
        top: 0;
        left: 100%;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}

@keyframes button-hovered {
    0%, 100%{
        top: 0;
        left: 0;
    }25%{
        top: 100%;
        left: 0;
        -webkit-transform: translateY(-100%);
                transform: translateY(-100%);
    }50%{
        top: 100%;
        left: 100%;
        -webkit-transform: translateY(-100%) translateX(-100%);
                transform: translateY(-100%) translateX(-100%);
    }75%{
        top: 0;
        left: 100%;
        -webkit-transform: translateX(-100%);
                transform: translateX(-100%);
    }
}
 

@-webkit-keyframes swoosh {
    0% {
        right: -25%;
        opacity: 1;
    } 10% {
        right: 100%;
        opacity: 0;
    } 100% {
        right: 100%;
        opacity: 0;
    }
}
 

@keyframes swoosh {
    0% {
        right: -25%;
        opacity: 1;
    } 10% {
        right: 100%;
        opacity: 0;
    } 100% {
        right: 100%;
        opacity: 0;
    }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {  
  display: grid;
  position: relative;
  grid-template-rows: 10vh 10vh auto;
  background-color:  rgb(105, 58, 105);
  min-height: 100vh;
}

@media only screen and (max-width: 35em) {
  .container {
    grid-template-rows: 7vh 7vh auto;
  }
}

.heading-container {
  grid-row: 1 / span 3;
  grid-column: 1 / span 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.heading {
  font-family: 'Racing Sans One', cursive;
  font-weight: 100;
  text-align: center;
  text-shadow: 1px 1px 0px rgb(94, 8, 102), 2px 2px 0px rgb(178, 54, 182), 3px 3px 0px rgb(134, 93, 175), 4px 4px 10px rgba(255, 218, 163, 0.596), 4px 4px 20px rgba(255, 255, 255, 0.541), 70px 0 105px rgba(255, 255, 255, .8), -70px 0 105px rgba(255, 255, 255, .8),
  20px 20px 100px rgba(255, 255, 255, 0.459), -20px 20px 100px rgba(255, 255, 255, 0.459), 20px -20px 100px rgba(255, 255, 255, 0.459), -20px -20px 100px rgba(255, 255, 255, 0.459);
  font-size: 6rem;
  color: rgba(0, 0, 0, 0.473);
}

@media only screen and (max-width: 35em) {
  .heading {
    font-size: 3.5rem;
  }
}

::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
  box-shadow: inset 0 0 10px -1px rgba(0, 0, 0, .2);
  
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgb(136, 74, 136)), to(rgb(7, 6, 6)));
  background-image: linear-gradient(rgb(136, 74, 136), rgb(7, 6, 6));
  box-shadow: inset 0 0 3px 1px rgba(0, 0, 0, .3); 
}
